// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Subject } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetSubject() {
  const [data, setData] = useState<Response<Subject[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.subject.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createSubject(subject: any) {
  const { data } = await sdk.course.offline.subject.create(subject);

  return data;
}

// ----------------------------------------------------------------------

export async function updateSubject(subject: any, id: string) {
  const { data } = await sdk.course.offline.subject.update(id, subject);

  return data;
}
