// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Transcript } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetTranscript() {
  const [data, setData] = useState<Response<Transcript[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.transcript.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createTranscript(transcript: any) {
  const { data } = await sdk.course.offline.transcript.create(transcript);

  return data;
}

// ----------------------------------------------------------------------

export async function updateTranscript(transcript: any, id: string) {
  const { data } = await sdk.course.offline.transcript.update(id, transcript);

  return data;
}
