// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Class, Response } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetClass() {
  const [data, setData] = useState<Response<Class[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.class.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createClass(classroom: any) {
  const { data } = await sdk.course.offline.class.create(classroom);

  return data;
}

// ----------------------------------------------------------------------

export async function updateClass(classroom: any, id: string) {
  const { data } = await sdk.course.offline.class.update(id, classroom);

  return data;
}
