// react
import { useState } from 'react';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
// MUI
import Container from '@mui/material/Container';
import FormControl from '@mui/material/FormControl';
import Grid from '@mui/material/Grid';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
// api
import useGetClass from 'src/api/controllers/class';
import useGetSubject from 'src/api/controllers/subject';
import useGetTranscript from 'src/api/controllers/transcript';
// route
import { paths } from 'src/routes/paths';
import DetailsChart from './details-chart';
import TotalChart from './total-chart';

// ----------------------------------------------------------------------

export default function TotalTranscriptView() {
  const { data: classes } = useGetClass();

  const { data: subjects } = useGetSubject();

  const { data: transcripts } = useGetTranscript();

  const [subjectId, setSubjectId] = useState();

  const [gradeId, setGradeId] = useState();

  const [classId, setCLassId] = useState();

  const filterGrade = subjects?.filter((subject: any) => subject?.id === subjectId)[0]?.grades;

  const filterClass = classes?.filter((classroom: any) => classroom?.grade?.id === gradeId);

  const filterTranscript =
    transcripts?.filter((transcript: any) => transcript?.class?.id === classId) || [];

  return (
    <>
      <CustomBreadcrumbs
        heading="Thống kê điểm học sinh"
        links={[
          { name: 'Danh mục' },
          {
            name: 'Bảng điểm',
            href: paths.home.transcript.root,
          },
          { name: 'Thống kê' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Stack direction="row" sx={{ px: 3, pb: 3 }} spacing={2}>
        <FormControl>
          <InputLabel>Lựa chọn môn học...</InputLabel>
          <Select
            label="Lựa chọn môn học..."
            value={subjectId}
            onChange={(event: any) => setSubjectId(event.target.value)}
            sx={{ width: 300 }}
          >
            {subjects?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {option?.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn khối lớp...</InputLabel>
          <Select
            disabled={!subjectId}
            label="Lựa chọn khối lớp..."
            value={gradeId}
            onChange={(event: any) => setGradeId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterGrade?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Khối lớp ${option?.label}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <InputLabel>Lựa chọn lớp học...</InputLabel>
          <Select
            disabled={!gradeId || !subjectId}
            label="Lựa chọn lớp học..."
            value={classId}
            onChange={(event: any) => setCLassId(event.target.value)}
            sx={{ width: 300 }}
          >
            {filterClass?.map((option: any) => (
              <MenuItem key={option?.id} value={option?.id}>
                {`Lớp ${option?.grade?.label}${option?.name}`}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Stack>

      <Container maxWidth={false}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <TotalChart
              sx={{ height: '100%' }}
              title="Biểu đồ điểm thi"
              chart={{
                series: [
                  {
                    label: 'Điểm giỏi 8 - 10',
                    value:
                      filterTranscript?.filter(
                        (trans: any) => trans?.final >= 8 && trans?.final <= 10
                      )?.length || 0,
                  },
                  {
                    label: 'Điểm khá 6.5 - 8',
                    value:
                      filterTranscript?.filter(
                        (trans: any) => trans?.final >= 6.5 && trans?.final < 8
                      )?.length || 0,
                  },
                  {
                    label: 'Điểm trung bình 5 - 6.5',
                    value:
                      filterTranscript?.filter(
                        (trans: any) => trans?.final >= 5 && trans?.final < 6.5
                      )?.length || 0,
                  },
                  {
                    label: 'Điểm yếu 3.5 - 5',
                    value:
                      filterTranscript?.filter(
                        (trans: any) => trans?.final >= 3.5 && trans?.final < 5
                      )?.length || 0,
                  },
                  {
                    label: 'Điểm kém < 3.5',
                    value:
                      filterTranscript?.filter(
                        (trans: any) => trans?.final >= 0 && trans?.final < 3.5
                      )?.length || 0,
                  },
                ],
              }}
            />
          </Grid>

          <Grid item xs={12} md={8}>
            <DetailsChart
              sx={{ height: '100%' }}
              title="Chi tiết điểm của học sinh"
              subheader="Tính trong một năm học"
              defaultValue={filterTranscript[0]?.student?.name}
              chart={{
                categories: [
                  'Điểm giữa kỳ I',
                  'Điểm điểm cuối kỳ I',
                  'Điểm tổng kết kỳ I',
                  'Điểm giữa kỳ II',
                  'Điểm cuối kỳ II',
                  'Điểm tổng kết kỳ II',
                  'Điểm tổng kết năm',
                ],
                // @ts-ignore
                series: filterTranscript?.map((transcript: any) => ({
                  year: transcript?.student?.name,
                  data: [
                    {
                      name: 'Điểm học kỳ I',
                      data: [
                        Number(transcript?.t1Mid).toFixed(2),
                        Number(transcript?.t1Final).toFixed(2),
                        Number(transcript?.finalp1).toFixed(2),
                        Number(transcript?.t2Mid).toFixed(2),
                        Number(transcript?.t2Final).toFixed(2),
                        Number(transcript?.finalp2).toFixed(2),
                        Number(transcript?.final).toFixed(2),
                      ],
                    },
                  ],
                })),
              }}
            />
          </Grid>
        </Grid>
      </Container>
    </>
  );
}
